import { DataFetcher } from './interfaces';

interface FetchDictionaryDataOptions {
    dataFetcher: DataFetcher;
    sitecoreApiHost: string;
    jssAppName: string;
    sitecoreApiKey: string;
    sitecoreSiteName: string;
}

interface DictionaryServiceResponse {
    data?: {
        phrases?: any;
    };
}

export function fetchDictionaryData(
    language: string,
    { dataFetcher, sitecoreApiHost, jssAppName, sitecoreApiKey, sitecoreSiteName }: FetchDictionaryDataOptions
) {
    const dictionaryServiceUrl = `${sitecoreApiHost}/sitecore/api/jss/dictionary/${jssAppName}/${language}?sc_apikey=${sitecoreApiKey}&sc_site=${sitecoreSiteName}`;
    return dataFetcher<DictionaryServiceResponse>(dictionaryServiceUrl).then((response) => {
        if (response.data && response.data.phrases) {
            return response.data.phrases;
        }
        return null;
    });
}
